import { Injectable, Inject } from '@angular/core'
import { HttpClient } from '@angular/common/http'
import { AuthenticationService } from '../../fx-security/services/authentication.service'
import { Role } from '../../fx-security/domain/role'
import { SecurityConfig } from '../../fx-security/fx-security.module'
import { SECURITY_CONFIG } from '../../fx-security/domain/security-config'
import { AuthorizationService } from '../../fx-security/services/authorization.service'
import { BoundarySelectorResource } from '../domain/boundary-selector-resource'
import { map } from 'rxjs/operators'
import { Observable } from 'rxjs'

@Injectable()
export class BoundaryDataService {

    public securityConfig: SecurityConfig

    constructor(
        private http: HttpClient,
        private authenticationService: AuthenticationService,
        private authorizationService: AuthorizationService,
        @Inject(SECURITY_CONFIG) securityConfig: SecurityConfig) {
        this.securityConfig = securityConfig
    }

    public getBoundariesForUserScope(): Observable<BoundarySelectorResource[]> {
        return this.http.get<BoundarySelectorResource[]>(this.getApiUrl()).pipe(map((boundarySelectorResources: BoundarySelectorResource[]) => {
            return this.filterBoundariesForUserScope(boundarySelectorResources)
        }))
    }

    public filterBoundariesForUserScope(boundarySelectorResources: BoundarySelectorResource[]): BoundarySelectorResource[] {
        const filteredBoundarySelectorResources: BoundarySelectorResource[] = []

        if (this.authorizationService.hasDevopsRole()) {
            return boundarySelectorResources
        }

        this.authenticationService.getPayload().roles.forEach((role: Role) => {
            if (role.boundaries[this.securityConfig.boundary_name]) {
                role.boundaries[this.securityConfig.boundary_name].forEach((scope: string) => {
                    const boundaryFound = boundarySelectorResources.find(boundary => boundary.boundaryValueKey === scope)
                    if (boundaryFound) {
                        filteredBoundarySelectorResources.push(boundaryFound)
                    }
                })
            }
        })

        return filteredBoundarySelectorResources
    }

    private getApiUrl(): string {
        return `${this.securityConfig.authorization_service_url}/api/v1/domains/${this.securityConfig.app_domain}/apps/${this.securityConfig.app_name}/boundaryvaluesminified/`
    }
}
