export function getShuffleArray(originalArray: Array<any>): Array<any> {
  const array = Object.assign([], originalArray)
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]]
  }
  return array
}

export function getUniqueArray(arrayFirst: Array<any>, propertyKey: string): Array<any> {
  const unique = arrayFirst
    .map(item => item[propertyKey])
    .map((item, index, final) => final.indexOf(item) === index && index)
    .filter(index => arrayFirst[index])
    .map(index => arrayFirst[index])
  return unique
}
