import { Inject, Injectable } from '@angular/core'
import { Observable } from 'rxjs'
import { HttpClient } from '@angular/common/http'
import { FxUserNotification } from '../domain/fx-user-notification'
import { NOTIFICATION_CONFIG } from '../domain/fx-notification-config'
import { FxNotificationConfig } from '../fx-user-notification.module'
import { IFxUserNotificationLocalDevelopmentService } from './i-fx-user-notification.service'

@Injectable()
export class FxUserNotificationService implements IFxUserNotificationLocalDevelopmentService {

  public baseUrl: string
  constructor(private http: HttpClient,
              @Inject(NOTIFICATION_CONFIG) notificationConfig: FxNotificationConfig) {
    this.baseUrl = notificationConfig.notification_service_url
  }
  public getNotifications(): Observable<Array<FxUserNotification>> {
    return this.http.get<Array<FxUserNotification>>(`${this.baseUrl}/api/v1/read-unread-usernotifications`)
  }

  public postUserReadLog(): Observable<Object> {
    return this.http.post(`${this.baseUrl}/api/v1/userlogs`, {})
  }
}

