import { ChangeDetectorRef, Directive, EmbeddedViewRef, Input, OnDestroy, TemplateRef, ViewContainerRef } from '@angular/core'
import { Subscription } from 'rxjs'
import { AuthenticationService } from '../services/authentication.service'
import { AuthorizationService } from '../services/authorization.service'

@Directive({
  selector: '[appHasRole]'
})
export class HasRoleDirective implements OnDestroy {

  private _context: HasRoleContext = new HasRoleContext()
  private _thenTemplateRef: TemplateRef<HasRoleContext> | null = null
  private _elseTemplateRef: TemplateRef<HasRoleContext> | null = null
  private _thenViewRef: EmbeddedViewRef<HasRoleContext> | null = null
  private _elseViewRef: EmbeddedViewRef<HasRoleContext> | null = null

  private loginEventsSubscription: Subscription

  constructor(private _viewContainer: ViewContainerRef,
    templateRef: TemplateRef<HasRoleContext>,
    authService: AuthenticationService,
    private authorizationService: AuthorizationService,
    private changeDetector: ChangeDetectorRef) {
    this._thenTemplateRef = templateRef

    this.loginEventsSubscription = authService.loginEvents.subscribe(() => {
      this._updateView()
      this.changeDetector.markForCheck()
    })
  }

  public ngOnDestroy(): void {
    if (this.loginEventsSubscription) {
      this.loginEventsSubscription.unsubscribe()
    }
  }

  @Input()
  set hasRole(condition: any) {
    this._context.$implicit = this._context.hasRole = condition
    this._updateView()
  }

  @Input()
  set hasRoleFactory(value: string) {
    this._context.factory = value
    this._updateView()
  }

  @Input()
  set hasRoleThen(templateRef: TemplateRef<HasRoleContext>) {
    this._thenTemplateRef = templateRef
    this._thenViewRef = null  // clear previous view if any.
    this._updateView()
  }

  @Input()
  set hasRoleElse(templateRef: TemplateRef<HasRoleContext>) {
    this._elseTemplateRef = templateRef
    this._elseViewRef = null  // clear previous view if any.
    this._updateView()
  }

  private _updateView(): void {
    let hasRole = false
    if (this._context && this._context.$implicit) {
      if (typeof this._context.factory === 'string' && this._context.factory.length > 0) {
        hasRole = this.authorizationService.hasRoleForBoundary(this._context.$implicit, this._context.factory)
      } else {
        hasRole = this._context && this._context.$implicit && this.authorizationService.hasRole(this._context.$implicit)
      }
    }
    if (hasRole) {
      if (!this._thenViewRef) {
        this._viewContainer.clear()
        this._elseViewRef = null
        if (this._thenTemplateRef) {
          this._thenViewRef =
            this._viewContainer.createEmbeddedView(this._thenTemplateRef, this._context)
        }
      }
    } else {
      if (!this._elseViewRef) {
        this._viewContainer.clear()
        this._thenViewRef = null
        if (this._elseTemplateRef) {
          this._elseViewRef =
            this._viewContainer.createEmbeddedView(this._elseTemplateRef, this._context)
        }
      }
    }
  }
}

export class HasRoleContext {
  public $implicit = null
  public hasRole = null
  public factory: string = null
}
