import { ChangeDetectorRef, Component, Input, OnDestroy, OnInit } from '@angular/core'
import { Subscription } from 'rxjs'
import { AuthenticationService } from '../../services/authentication.service'
import { UserService } from '../../services/user.service'
import { IconProp } from '@fortawesome/fontawesome-svg-core'
import { faUser } from '@fortawesome/free-solid-svg-icons/faUser'

@Component({
  selector: 'app-fx-login',
  templateUrl: './fx-login.component.html',
  styleUrls: ['./fx-login.component.scss'],
})
export class FxLoginComponent implements OnInit, OnDestroy {

  @Input()
  public btnStyle = ''

  @Input()
  public dropDownStyling = ''

  @Input()
  public languageSelectorRadioGroupStyling = 'fx-styling'

  @Input()
  public icon = 'fa-user'

  public fasUser: IconProp = faUser

  public username: string

  private subscriptions: Subscription = new Subscription()

  constructor(private authenticationService: AuthenticationService,
              private userService: UserService,
              private changeDetector: ChangeDetectorRef) {
  }

  public ngOnInit(): void {
    this.subscriptions.add(this.authenticationService.loginEvents.subscribe(() => {
      this.username = this.userService.getUsername()
      this.changeDetector.markForCheck()
    }))
  }

  public isNotLoggedIn(): boolean {
    return !this.username || this.username.trim().length === 0
  }

  public login(): void {
    this.authenticationService.loginWithRedirectUrl()
  }

  public ngOnDestroy(): void {
    this.subscriptions.unsubscribe()
  }

  public setIcon(selectedIcon?: string): IconProp {
    switch (selectedIcon) {
      case 'fasUser': {
        return this.fasUser
      }
      default: {
        return this.fasUser
      }
    }
  }
}
