import { Pipe, PipeTransform } from '@angular/core'
import { TranslatePipeMock } from './translate.pipe.mock'

@Pipe({name: 'translateWithPluralization'})
export class TranslateWithPluralizationPipeMock extends TranslatePipeMock implements PipeTransform {
  public transform(value: any, ...args: any): any {
    return 'tr:' + value
  }
}
