import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core'
import { DropdownItem } from './domain/dropdown-item'

@Component({
  selector: 'app-fx-dropdown',
  templateUrl: './fx-dropdown.component.html',
  styleUrls: ['./fx-dropdown.component.scss']
})
export class FxDropdownComponent implements OnInit {

  @Input() public options: DropdownItem[]

  @Input() public placeholder?: string

  @Input() public highlightActive?: boolean

  @Input() public currentItem?: DropdownItem

  @Input() public cssClass = ''

  @Input() public filterable = false

  @Output() public currentItemChange = new EventEmitter<DropdownItem>()

  private filterInput: string

  public ngOnInit(): void {
    if (!this.placeholder && this.options[0]) {
      this.currentItem = this.options[0]
    }
  }

  public clickItem(item: DropdownItem): void {
    if (item && !item.disabled) {
      this.currentItem = item
      if (item.onSelectAction) {
        item.onSelectAction()
      }
      this.currentItemChange.emit(item)
    }
  }

  public getComputedLabel(item: DropdownItem): string {
    return item.label ? item.label : item.key
  }

  public isCurrentItem(item: DropdownItem): boolean {
    return item === this.currentItem
  }

  public isItemHighlighted(item: DropdownItem): boolean {
    return this.isCurrentItem(item) && this.highlightActive
  }

  public getSelectedItem(): DropdownItem {
    return this.currentItem
  }

  public setSelectedItem(item: DropdownItem): void {
    this.currentItem = item
    this.currentItemChange.emit(item)
  }

  public setFilterText(searchString: string): void {
    this.filterInput = searchString
  }

  public getFilteredList(entries: DropdownItem[]): any {
    if (this.filterable && this.filterInput && this.filterInput.length > 0) {
      return entries.filter(e => e.key.concat(e.label).toLowerCase().includes(this.filterInput.toLowerCase()))
    } else {
      return entries
    }
  }
}
