import { Component, Input, OnInit, OnDestroy } from '@angular/core'
import { IconProp, SizeProp } from '@fortawesome/fontawesome-svg-core'
import { faBell } from '@fortawesome/free-solid-svg-icons'
import { FxUserNotificationService } from '../../services/fx-user-notification.service'
import { FxUserNotification, priorityMap } from '../../domain/fx-user-notification'
import { Subscription, timer } from 'rxjs'

@Component({
  selector: 'app-fx-user-notification',
  templateUrl: './fx-user-notification.component.html',
  styleUrls: ['./fx-user-notification.component.scss']
})
export class FxUserNotificationComponent implements OnInit, OnDestroy {

  public opened = false

  @Input()
  public icon: IconProp = faBell

  @Input()
  public color = 'white'

  @Input()
  public size: SizeProp = '2x'

  @Input()
  public refreshInterval = 60000

  @Input()
  public charsOfShortText = 50

  public userNotificationList: Array<FxUserNotification> = []
  public userNotificationUnreadList: Array<FxUserNotification> = []
  public userNotificationReadList: Array<FxUserNotification> = []

  public notificationCount: number
  public intervalTimeSubscription

  private userNotificationSubscription: Subscription

  constructor(private userNotificationService: FxUserNotificationService) {
  }

  public getNotificationIconColor(): { color: string } {
    return {color: this.color}
  }

  public ngOnInit(): void {
    this.intervalTimeSubscription = timer(0, this.refreshInterval).subscribe(() => {
      this.receiveNotifications()
    })
  }

  public receiveNotifications(): void {
    this.userNotificationSubscription = this.userNotificationService.getNotifications().subscribe(res => {
      this.sortNotificationsAndRemember(res)
    })
  }

  public toggleNotificationArea(): void {
    this.opened = !this.opened
    if (!this.opened) {
      this.postReadNotificationLogAndRefreshNotifications()
    }
  }

  public sortNotificationsAndRemember(notificationData: Array<FxUserNotification>): void {
    this.userNotificationReadList = notificationData.filter((notification) => {
      return notification.read
    })
    this.userNotificationUnreadList = notificationData.filter((notification) => {
      return !notification.read
    })
    this.userNotificationList = this.sortNotification(this.userNotificationUnreadList).concat(this.sortNotification(this.userNotificationReadList))
    this.notificationCount = this.userNotificationUnreadList.length
  }

  public sortNotification(notificationData: Array<FxUserNotification>): Array<FxUserNotification> {
    return notificationData.sort((notification1, notification2) => {
      {
        if (priorityMap.get(notification1.severity) < priorityMap.get(notification2.severity)) {
          return -1
        }
        if (priorityMap.get(notification1.severity) > priorityMap.get(notification2.severity)) {
          return 1
        }
        return this.compareUpdateTime(new Date(notification1.updateTimestamp), new Date(notification2.updateTimestamp)  )
      }
    })
  }

  public postReadNotificationLogAndRefreshNotifications(): void {
    this.userNotificationService.postUserReadLog().toPromise().then(() => {
      this.receiveNotifications()
    })
  }

  public ngOnDestroy(): void {
    if (this.intervalTimeSubscription) {
      this.intervalTimeSubscription.unsubscribe()
    }
    if (this.userNotificationSubscription) {
      this.userNotificationSubscription.unsubscribe()
    }
  }

  private compareUpdateTime(ts1: Date, ts2: Date): number {
    if (ts1.getTime() < ts2.getTime()) {
      return 1
    } else if (ts1.getTime() > ts2.getTime()) {
      return -1
    }
    return 0
  }
}

