import { Component, Input } from '@angular/core'

export interface ConfidentialityLogo {
  type: string
  path: string
}

@Component({
  selector: 'app-fx-confidentiality-logo',
  templateUrl: './fx-confidentiality-logo.component.html',
  styleUrls: ['./fx-confidentiality-logo.component.scss']
})
export class FxConfidentialityLogoComponent {

  public readonly confidentialityLogo: ConfidentialityLogo[] = [
    {type: 'public', path: '/assets/images/confidentiality_logos/VWAG_Public.svg'},
    {type: 'public_scaled', path: '/assets/images/confidentiality_logos/VWAG_Public_scaled.svg'},
    {type: 'internal', path: '/assets/images/confidentiality_logos/VWAG_Internal.svg'},
    {type: 'internal_scaled', path: '/assets/images/confidentiality_logos/VWAG_Internal_scaled.svg'},
    {type: 'confidential', path: '/assets/images/confidentiality_logos/VWAG_Confidential.svg'},
    {type: 'confidential_scaled', path: '/assets/images/confidentiality_logos/VWAG_Confidential_scaled.svg'},
    {type: 'secret', path: '/assets/images/confidentiality_logos/VWAG_Secret.svg'},
    {type: 'secret_scaled', path: '/assets/images/confidentiality_logos/VWAG_Secret_scaled.svg'},
  ]

  @Input()
  public logoType = 'internal'

  @Input()
  public logoHeight = 60

  @Input()
  public logoColor: string

  constructor() { }

  public getPath(): string {
    return this.confidentialityLogo.find(logo => logo.type === this.logoType).path
  }

}
