import { Component, OnInit, Inject } from '@angular/core'
import { Router } from '@angular/router'
import { SecurityConfig } from '../../fx-security.module'
import { SECURITY_CONFIG } from '../../domain/security-config'

@Component({
    selector: 'app-no-permissions',
    templateUrl: './no-permissions.component.html',
    styleUrls: ['./no-permissions.component.scss']
})
export class NoPermissionsComponent implements OnInit {

    public ursLink: string
    protected securityConfig: SecurityConfig

    constructor(private router: Router, @Inject(SECURITY_CONFIG) securityConfig: SecurityConfig) {
        this.securityConfig = securityConfig
    }

    public ngOnInit(): void {
        this.ursLink = this.securityConfig.authorization_ui_url
    }

}
