import { Injectable } from '@angular/core'
import { BehaviorSubject } from 'rxjs'
import { BoundarySelectorResource } from '../domain/boundary-selector-resource'

@Injectable()
export class BoundarySelectorDataService {

    public boundaryResourceList$ = new BehaviorSubject<BoundarySelectorResource[]>([])
    public selectedBoundary$ = new BehaviorSubject<BoundarySelectorResource>(null)
    private cachedBoundary

    constructor() {
    }

    public setBoundaryResourceList(boundaryResourceList: BoundarySelectorResource[]): void {
        if (this.boundaryResourceList$.getValue() === boundaryResourceList) {
            return
        }

        this.boundaryResourceList$.next(boundaryResourceList)

        if (this.cachedBoundary != null) {
            this.setSelectedBoundaryByKey(this.cachedBoundary)
            this.cachedBoundary = null
        }

        if (this.selectedBoundary$.getValue() == null) {
            this.setSelectedBoundaryToDefault()
        }
    }

    public setSelectedBoundaryByKey(boundaryKey: string): void {
        const foundBoundary: BoundarySelectorResource = this.getBoundaryByKey(boundaryKey)
        if (this.boundaryResourceList$.getValue().length < 1) {
            this.setSelectedBoundaryToDefault()
            this.cacheBoundaryKey(boundaryKey)
            return
        }

        if (!foundBoundary) {
            this.setSelectedBoundaryToDefault()
            return
        }

        if (this.selectedBoundary$.getValue() === foundBoundary) {
            return
        }

        this.selectedBoundary$.next(foundBoundary)
    }

    public setSelectedBoundary(boundary: BoundarySelectorResource): void {
        this.setSelectedBoundaryByKey(boundary.boundaryValueKey)
    }

    private getBoundaryByKey(boundaryKey: string): BoundarySelectorResource {
        return this.boundaryResourceList$.getValue().find(x => x.boundaryValueKey === boundaryKey)
    }

    private setSelectedBoundaryToDefault(): void {
        if (this.boundaryResourceList$.getValue() == null || this.boundaryResourceList$.getValue().length < 1) {
            this.selectedBoundary$.next(null)
            return
        }

        this.selectedBoundary$.next(this.boundaryResourceList$.getValue()[0])
    }

    private cacheBoundaryKey(boundaryKey: string): void {
        if (this.cachedBoundary === boundaryKey) {
            return
        }

        this.cachedBoundary = boundaryKey
    }
}
