import { Component, Input, OnInit } from '@angular/core'
import { ActivatedRoute, UrlSegment } from '@angular/router'
import { IconProp, SizeProp } from '@fortawesome/fontawesome-svg-core'
import { faQuestionCircle as farQuestionCircle } from '@fortawesome/free-regular-svg-icons/faQuestionCircle'
import { faQuestion } from '@fortawesome/free-solid-svg-icons'
import { faQuestionCircle as fasQuestionCircle } from '@fortawesome/free-solid-svg-icons/faQuestionCircle'
import { RoutingPathResource } from './domain/routing-path-resource'

@Component({
  selector: 'app-fx-wiki',
  templateUrl: './fx-wiki.component.html',
  styleUrls: ['./fx-wiki.component.scss'],
})
export class FxWikiComponent implements OnInit {

  public fasQuestion: IconProp = faQuestion
  public fasQuestionCircle: IconProp = fasQuestionCircle
  public farQuestionCircle: IconProp = farQuestionCircle

  @Input()
  public icon = 'farQuestionCircle'

  @Input()
  public color = 'black'

  @Input()
  public size: SizeProp = '2x'

  public defaultRoutingPath = new RoutingPathResource(RegExp(''), 'https://soco.volkswagen.com/wikis/')

  @Input()
  public routingPathArray: RoutingPathResource[]

  public wikiUrl: string

  constructor(private activatedRoute: ActivatedRoute) {
  }

  public ngOnInit(): void {
    this.activatedRoute.url.subscribe(url => this.wikiUrl = this.getRouteFromRegex(url))
  }

  public setIcon(selectedIcon?: string): IconProp {
    switch (selectedIcon) {
      case 'fasQuestion': {
        return this.fasQuestion
      }
      case 'farQuestionCircle': {
        return this.farQuestionCircle
      }
      case 'fasQuestionCircle': {
        return this.fasQuestionCircle
      }
      default: {
        return this.farQuestionCircle
      }
    }
  }

  public setIconColor(): { color: string } {
    return {color: this.color}
  }

  private getRouteFromRegex(urlArray: UrlSegment[]): string {
    const url = urlArray.join('/')
    if (this.routingPathArray) {
      for (const routingPath of this.routingPathArray) {
        if (url.match(routingPath.regex)) {
          return routingPath.url
        }
      }
    }
    return this.defaultRoutingPath.url
  }
}
