import { DEBUG, FACILITY } from '../constants/local-storage.constants'

export function setItem(key: string, value: string): void {
  if (!key) {
    return
  }
  localStorage.setItem(key, value)
}

export function deleteItem(key: string): void {
  if (!key) {
    return
  }
  localStorage.removeItem(key)
}

export function getItem(key: string): string {
  if (!key) {
    return null
  }
  return localStorage.getItem(key)
}

export function setFacility(facility: string): void {
  setItem(FACILITY, facility)
}

export function getFacility(): string {
  return getItem(FACILITY)
}

export function deleteFacility(): void {
  deleteItem(FACILITY)
}

export function setDebugMode(debug: boolean): void {
  setItem(DEBUG, debug ? '1' : '0')
}

export function isDebugMode(): boolean {
  return getItem(DEBUG) === '1'
}

export function deleteDebugMode(): void {
  deleteItem(DEBUG)
}
