import { TranslateService } from '@ngx-translate/core'
import { DatatableColumnTooltip } from '../interfaces/datatable-column'

export function getTooltip(tooltip: DatatableColumnTooltip | DatatableColumnTooltip[], translateService: TranslateService, row?: any): string | undefined {
  if (!tooltip || !translateService) {
    return undefined
  }
  let tipObj: DatatableColumnTooltip
  if (Array.isArray(tooltip)) {
    const tipArray: DatatableColumnTooltip[] = [...tooltip as DatatableColumnTooltip[]]
    if (!tipArray || tipArray.length === 0) {
      return undefined
    }
    const tripFromArray = tipArray.find(tip => tip.showCondition(row))
    if (tripFromArray) {
      tipObj = tripFromArray
    } else {
      return undefined
    }
  } else {
    tipObj = tooltip as DatatableColumnTooltip
    if (!tipObj || (tipObj.showCondition && !tipObj.showCondition(row))) {
      return undefined
    }
  }
  return tipObj ? translateService.instant(tipObj.text) + (tipObj.column ? ` (${row[tipObj.column]})` : '') : undefined
}
