import { Inject, Injectable } from '@angular/core'
import { SecurityConfig } from '../fx-security.module'
import { HttpClient, HttpErrorResponse, HttpResponse } from '@angular/common/http'
import { IdpIdentityToken } from './authentication.service'
import { Observable, throwError } from 'rxjs'
import { catchError, map } from 'rxjs/operators'
import { SECURITY_CONFIG } from '../domain/security-config'

@Injectable()
export class LoginService {
  public securityConfig: SecurityConfig

  private ursUrl = 'api/v1/auth'

  constructor(private http: HttpClient,
    @Inject(SECURITY_CONFIG) securityConfig: SecurityConfig) {

    this.securityConfig = securityConfig
  }

  public login(idToken: IdpIdentityToken): Observable<string> {
    const url: string = this.securityConfig.authorization_service_url + this.ursUrl

    return this.http
      .post(url, idToken, { observe: 'response', responseType: 'text' })
      .pipe(
        map((response: HttpResponse<any>) =>
          response.body
        ),
        catchError((error: HttpErrorResponse) =>
          this.handleError(error)
        )
      )
  }

  private handleError(error: HttpErrorResponse): Observable<never> {
    const errorMessage: string = error.message ? error.message : error.toString()
    console.error(errorMessage)

    return throwError(errorMessage)
  }
}
