import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core'
import { IconProp, SizeProp } from '@fortawesome/fontawesome-svg-core'
import { faCalendar as farCalendar } from '@fortawesome/free-regular-svg-icons/faCalendar'
import { faCalendarAlt as farCalendarAlt } from '@fortawesome/free-regular-svg-icons/faCalendarAlt'
import { faCalendar as fasCalendar } from '@fortawesome/free-solid-svg-icons/faCalendar'
import { faCalendarAlt as fasCalendarAlt } from '@fortawesome/free-solid-svg-icons/faCalendarAlt'
import { faCalendarWeek } from '@fortawesome/free-solid-svg-icons/faCalendarWeek'
import { NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import * as moment_ from 'moment'
import { delay, tap } from 'rxjs/operators'
import { WeekResource } from './domain/week-resource'

const DATE_FORMAT = 'YYYY-MM-DD'
const WEEK_START = 1
const moment = moment_

@Component({
  selector: 'app-fx-week-selector',
  templateUrl: './fx-week-selector.component.html',
  styleUrls: ['./fx-week-selector.component.scss'],
})
export class FxWeekSelectorComponent implements OnInit, OnChanges {

  public fasCalendarWeek: IconProp = faCalendarWeek
  public fasCalendarAlt: IconProp = fasCalendarAlt
  public farCalendarAlt: IconProp = farCalendarAlt
  public fasCalendar: IconProp = fasCalendar
  public farCalendar: IconProp = farCalendar

  @Input()
  public icon = 'fasCalendarWeek'

  @Input()
  public colorIcon = 'black'

  @Input()
  public sizeIcon: SizeProp = '1x'

  @Input()
  public btnStyle = ''

  @Input()
  public selectorStyling = ''

  @Input()
  public displayMonth = '2'

  @Input()
  public initDate: NgbDate

  @Input()
  public minDate: NgbDate

  @Input()
  public maxDate: NgbDate

  @Output()
  public selectionEvent: EventEmitter<WeekResource>

  @Input()
  public displayString: string

  public visible = false

  public disable = true

  public hoverDate: NgbDate
  public selectedDate: NgbDate
  public hoveredWeekNr: number
  public selectedWeekNr: number
  public showNavigation = true


  constructor(private calendar: NgbCalendar, private translateService: TranslateService) {

    this.selectionEvent = new EventEmitter<WeekResource>()

    // workaround for translation. Update the navigation component to load the new language immediately
    this.translateService.onLangChange.pipe(
      tap(() => this.showNavigation = false),
    ).pipe(
      delay(1),
      tap(() => this.showNavigation = true),
    ).subscribe()
  }

  public closeDatePicker(): void {
    this.visible = false
  }

  public ngOnInit(): void {
    if (!this.initDate) {
      return
    }
    this.disable = !this.displayString || this.displayString === ''
    this.selectedDate = this.initDate
  }

  public ngOnChanges(data: SimpleChanges): void {
    // FIXME: data should be checked, if its the correct property
    this.selectedDate = this.initDate
    this.selectedWeekNr = this.returnWeekNr(this.initDate)
    this.isInsideWeekSelection(this.selectedDate)
  }

  public isInsideWeekSelection(date: NgbDate): boolean {
    const weekNr = this.returnWeekNr(date)
    return this.selectedWeekNr === weekNr
  }

  public selectedWeekOfYear(): WeekResource {
    return new WeekResource(this.selectedDate.year, this.returnWeekNr(this.selectedDate))
  }

  public isInsideWeekHovered(date: NgbDate): boolean {
    const weekNr = this.returnWeekNr(date)
    return this.hoveredWeekNr === weekNr
  }

  public onHover(date: NgbDate): void {
    this.hoverDate = date
    this.hoveredWeekNr = this.returnWeekNr(date)
  }

  public onDateSelection(date: NgbDate): void {
    this.selectedDate = date
    this.selectedWeekNr = this.returnWeekNr(date)
    this.closeDatePicker()
    this.selectionEvent.emit(new WeekResource(this.returnWeekYear(date), this.selectedWeekNr))
  }

  public returnWeekNr(date: NgbDate): number {
    moment.locale('de')
    return moment(`${date.year}-${date.month}-${date.day}`, DATE_FORMAT).isoWeekday(WEEK_START).week()
  }

  public returnWeekYear(date: NgbDate): number {
    moment.locale('de')
    return moment(`${date.year}-${date.month}-${date.day}`, DATE_FORMAT).isoWeekday(WEEK_START).weekYear()
  }

  public setIcon(selectedIcon?: string): IconProp {
    switch (selectedIcon) {
      case 'fasCalendarAlt': {
        return this.fasCalendarAlt
      }
      case 'farCalendarAlt': {
        return this.farCalendarAlt
      }
      case 'fasCalendar': {
        return this.fasCalendar
      }
      case 'farCalendar': {
        return this.farCalendar
      }
      default: {
        return this.fasCalendarWeek
      }
    }
  }

  public setIconColor(): { colorIcon: string } {
    return {colorIcon: this.colorIcon}
  }

  public isDisabled = (date: NgbDate) => this.isDayDisabled(date)

  public isDayDisabled(date: NgbDate): boolean {
    return date.before(this.minDate) || date.after(this.maxDate)
  }

  public isToday(date: NgbDate): boolean {
    const weekNr = this.returnWeekNr(date)
    const calendarWeekNr = this.returnWeekNr(this.calendar.getToday())
    const weekYear = this.returnWeekYear(date)
    const calendarWeekYear = this.returnWeekYear(this.calendar.getToday())
    return weekYear === calendarWeekYear && weekNr === calendarWeekNr
      && !this.isInsideWeekSelection(date) && !this.isInsideWeekHovered(date)
  }
}
