import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, TemplateRef } from '@angular/core'
import { Subject, Subscription } from 'rxjs'
import { debounceTime, map } from 'rxjs/operators'

@Component({
  selector: 'app-fx-search-input',
  templateUrl: './fx-search-input.component.html',
  styleUrls: ['./fx-search-input.component.scss']
})
export class FxSearchInputComponent implements OnInit, OnDestroy {
  @Input()
  public searchInputValue: string

  @Input()
  public searchFieldMinLength = 3

  @Input()
  public required = false

  @Input()
  public clearSearchInputWithEscape = true

  @Input()
  public placeholder = 'i18n.search-input.placeholder'

  @Input()
  public requiredAlert = 'i18n.search-input.warnings.required'

  @Input()
  public minLengthAlert = 'i18n.search-input.warnings.minlength'

  @Input()
  public autocomplete: 'off' | 'on' = 'off'

  @Input()
  public id = 'searchInput'

  @Input()
  public name = 'searchInput'

  @Input()
  public title = ''

  @Input()
  public belowSearchInputTemplate: TemplateRef<any>

  @Input()
  public debounceTimeInMilliseconds = 700

  @Input()
  public trimSearchInput = true

  @Input()
  public style = ''

  @Output()
  public searchInputChangedEvent = new EventEmitter<string>()

  public searchInputChanged = new Subject<string>()

  private subscription = new Subscription()

  constructor() {
  }

  public ngOnInit(): void {
    this.subscription.add(this.searchInputChanged
      .pipe(
        map(search => {
          if (!this.trimSearchInput) {
            return ({searchValue: (search && search.length < this.searchFieldMinLength) ? null : search})
          }
          // at least three char have been typed into the search input (or value is empty, than clear filter)
          return ({searchValue: (search && search.trim().length < this.searchFieldMinLength) ? null : search})
        }),
        // delaying the search by x.x seconds before triggering the search API call
        debounceTime(this.debounceTimeInMilliseconds),
      )
      .subscribe(search => this.searchInputChangedEvent.next(search.searchValue))
    )
  }

  public ngOnDestroy(): void {
    this.subscription.unsubscribe()
  }

}
