import { Injectable } from '@angular/core'
import { Subject, BehaviorSubject } from 'rxjs'
import { FxHeaderConfigValidatorService } from './fx-header-config-validator.service'
import { FxHeaderConfigModel } from '../models/config/fx-header-config.model'

@Injectable()
export class FxHeaderConfigService {

    public config: Subject<FxHeaderConfigModel> = new BehaviorSubject<FxHeaderConfigModel>({
        appTitleShort: '',
        appTitle: '',
        routes: [],
        staticComponents: [],
        components: []
    })

    constructor(private fxheaderConfigValidatorService: FxHeaderConfigValidatorService) { }

    public setConfig(config: FxHeaderConfigModel): void {
        if (!this.fxheaderConfigValidatorService.validate(config)) {
            return
        }

        this.config.next(config)
    }

}
