import { Inject, Injectable } from '@angular/core'
import { Router } from '@angular/router'
import { BehaviorSubject, Observable } from 'rxjs'
import { map, tap } from 'rxjs/operators'
import { getBase64DecodeUrlSafe, getBase64EncodeUrlSafe } from '../../shared/helper/base64.helper'
import { Payload } from '../domain/payload'
import { SECURITY_CONFIG } from '../domain/security-config'
import { SecurityConfig } from '../fx-security.module'
import { LoginService } from './login.service'
import { RedirectorService } from './redirector.service'

interface HashParams {
  [key: string]: string
}

export type LoginEvent = 'loggedin' | 'loggedout'
export const TOKEN_KEY = 'URS_TOKEN'
export const ID_TOKEN_KEY = 'URS_ID_TOKEN'

export interface IdpIdentityToken {
  token: string
  state: string
  app: string
}

@Injectable()
export class AuthenticationService {

  protected securityConfig: SecurityConfig

  public isLoggedIn = false

  protected _token?: string
  get token(): string {
    return this._token || ''
  }

  set token(token: string) {
    this._token = token
  }

  public loginEvents: BehaviorSubject<LoginEvent>

  constructor(private loginService: LoginService,
              private router: Router,
              private redirectorService: RedirectorService,
              @Inject(SECURITY_CONFIG) securityConfig: SecurityConfig) {
    this.loginEvents = new BehaviorSubject<LoginEvent>('loggedout')
    this.initTokenFromStorage()
    this.securityConfig = securityConfig
  }

  public login(identityToken: IdpIdentityToken): Observable<IdpIdentityToken> {
    return this.loginService.login(identityToken)
      .pipe(
        tap((token: string) => {
          this.initLogin(token, identityToken)
          this.loginEvents.next('loggedin')
        }, () => {
          this.removeLoginInformation()
          this.router.navigateByUrl('/login-error')
        }),
        map(() =>
          identityToken,
        ),
      )
  }

  public logout(): void {
    // Check for ID token
    const idToken = localStorage.getItem(ID_TOKEN_KEY)
    this.removeLoginInformation()
    if (idToken) {
      this.redirectorService.redirectToUrl(this.buildIdpLogoutUrl(idToken))
    } else {
      this.router.navigateByUrl('/')
    }
  }

  public getAuthTokenHeaderString(): string {
    return 'Bearer ' + this.token
  }

  public initTokenFromStorage(): void {
    const storedToken: string = localStorage.getItem(TOKEN_KEY)
    if (storedToken == null) {
      return
    }
    try {
      const parts: string[] = storedToken.split('.')
      const payload = JSON.parse(atob(parts[1]))
      const until = new Date(payload.exp * 1000)
      const now = new Date()

      if (now < until) {
        this.token = storedToken
        this.isLoggedIn = true
        this.loginEvents.next('loggedin')
      }
    } catch (ex) {
      this.removeLoginInformation()
    }
  }

  public loginFromUrlToken(url: string): Observable<IdpIdentityToken> {
    const identityToken: IdpIdentityToken = this.extractTokenFromUrl(url)
    return this.login(identityToken)
  }

  public getPayload(): Payload {
    if (this.isLoggedIn) {
      const token = this.token
      const parts: string[] = token.split('.')
      if (parts.length >= 2) {
        return JSON.parse(atob(parts[1]))
      }
    }
    return {roles: [], username: ''}
  }

  public loginWithRedirectUrl(): void {
    this.redirectToIdpLogin(this.securityConfig.login_redirect_url)
  }

  public redirectToIdpLogin(targetUrl: string): void {
    this.redirectorService.redirectToUrl(this.buildIdpLoginUrl(targetUrl))
  }

  private initLogin(token: string, identityToken: IdpIdentityToken): void {
    this.token = token
    this.isLoggedIn = true
    localStorage.setItem(TOKEN_KEY, token)
    localStorage.setItem(ID_TOKEN_KEY, identityToken.token)
  }

  private removeLoginInformation(): void {
    this.token = undefined
    this.isLoggedIn = false
    localStorage.removeItem(TOKEN_KEY)
    localStorage.removeItem(ID_TOKEN_KEY)
    this.loginEvents.next('loggedout')
  }

  private buildIdpLoginUrl(state: string): string {
    return this.securityConfig.idp_base_url + 'auth'
      + '?response_type=id_token'
      + '&scope=openid'
      + '&nonce=' + this.generateNonce()
      + '&client_id=' + encodeURIComponent(this.securityConfig.client_id)
      + '&redirect_uri=' + encodeURIComponent(this.securityConfig.login_redirect_url)
      + '&state=' + encodeURIComponent(getBase64EncodeUrlSafe(state))
  }

  private mathFloorRandom(): string {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1)
  }

  private generateNonce(): string {
    let nonce = ''
    for (let i = 0; i < 8; i++) {
      nonce += this.mathFloorRandom()
    }
    return nonce
  }

  private extractTokenFromUrl(url: string): IdpIdentityToken {
    const hashPart = this.extractHashPart(url)
    const hashParams: HashParams = this.extractParameters(decodeURIComponent(hashPart))
    if (hashParams && hashParams.state && hashParams.id_token) {
      return {
        state: hashParams.state,
        token: hashParams.id_token,
        app: this.securityConfig.app_name,
      }
    }
    return null
  }

  private extractHashPart(url: string): string {
    const hashPosition = url.indexOf('#')
    if (hashPosition >= 0 && url.length > hashPosition) {
      return url.slice(hashPosition + 1)
    } else {
      return ''
    }
  }

  private extractParameters(paramString: string): HashParams {
    return paramString.split('&')
      .reduce((params: HashParams, keyValueText: string) => {
        if (keyValueText) {
          const keyValue = keyValueText.split('=')
          if (keyValue.length === 2) {
            const key = keyValue[0]
            const value = keyValue[1]
            const decodedValue = key === 'state' ? getBase64DecodeUrlSafe(value) : value
            params[key] = decodeURIComponent(decodedValue)
          }
        }
        return params
      }, {})
  }

  private buildIdpLogoutUrl(token: string): string {
    return this.securityConfig.idp_base_url + 'logout'
      + '?id_token=' + token
      + '&post_logout_redirect_uri=' + encodeURIComponent(this.securityConfig.logout_redirect_url)
  }
}
