import { Component, Input, OnInit } from '@angular/core'
import { FxUserNotification } from '../../domain/fx-user-notification'

@Component({
  selector: 'app-fx-user-single-notification-area',
  templateUrl: './fx-user-single-notification-area.component.html',
  styleUrls: ['./fx-user-single-notification-area.component.scss']
})
export class FxUserSingleNotificationAreaComponent implements OnInit {

  public completeNotification: FxUserNotification

  @Input()
  public set notification(notification: FxUserNotification) {
    this.completeNotification = notification
    this.updateVisibleMessageText()
  }

  @Input()
  public charsOfShortText = 150

  public isMessageShorted = false
  public visibleMessage = ''
  public show = false

  constructor() {
  }

  public ngOnInit(): void {
    this.updateVisibleMessageText()
  }

  public getSeverityStyle(notification: FxUserNotification): string {
    if (!notification) {
      return ''
    }
    if (!notification.read) {
      if (notification.severity === 'ERROR') {
        return 'alert alert-danger'
      } else if (notification.severity === 'WARNING') {
        return 'alert alert-warning'
      } else {
        return 'alert alert-info'
      }
    } else {
      return 'alert alert-secondary'
    }
  }

  public showToggle(): void {
    this.show = !this.show
    this.updateVisibleMessageText()
  }

  public getSeverityIcon(notification: FxUserNotification): string {
    if (!notification) {
      return ''
    }
    if (notification.severity === 'ERROR') {
      return 'fas fa-exclamation-circle'
    } else if (notification.severity === 'WARNING') {
      return 'fas fa-exclamation-triangle'
    } else {
      return 'fas fa-info-circle'
    }
  }

  private updateVisibleMessageText(): void {
    if (!this.completeNotification) {
      return
    }
    if (this.completeNotification.messageText.length > this.charsOfShortText && !this.show) {
      this.visibleMessage = this.completeNotification.messageText.slice(0, this.charsOfShortText)
      this.isMessageShorted = true
    } else {
      this.visibleMessage = this.completeNotification.messageText
    }
  }
}
