import { Component, Input } from '@angular/core'
import { TranslateService } from '@ngx-translate/core'
import { Language } from './domain/language'
import { FX_LANGUAGE } from '../fx-translation/fx-translation.component'

@Component({
  selector: 'app-fx-language-selector',
  templateUrl: './fx-language-selector.component.html',
  styleUrls: ['./fx-language-selector.component.scss']
})
export class FxLanguageSelectorComponent {

  @Input()
  public radioGroupStyling = ''

  public readonly languages: Language[] = [
    { locale: 'de', name: 'Deutsch' },
    { locale: 'en', name: 'English' }
  ]

  constructor(private translateService: TranslateService) {
  }

  public isSelected(language: Language): boolean {
    return language === this.selectedLanguage
  }

  get selectedLanguage(): Language {
    const langFromLocalStorage = localStorage.getItem(FX_LANGUAGE)
    const selectedLanguage = this.languages.find(lang => lang.locale === langFromLocalStorage)
    return selectedLanguage
  }

  public select(language: Language): void {
    const selectedLanguage = this.languages.find(lang => lang.locale === language.locale)
    if (!selectedLanguage) {
      return
    }

    this.translateService.use(language.locale)
    localStorage.setItem(FX_LANGUAGE, language.locale)
  }

}
