import { Type } from '@angular/core'
import { JestOriginalFunction } from '../../../shared/types/jest.types'
import { JestExtended, JestSpyObject } from '../../interfaces/jest-extended'

export function getAllFunctionNames<T extends Type<any>>(clazz: T): PropertyKey[] {
  if (typeof clazz === 'function') {
    clazz = clazz.prototype
  }
  let foundedFunctionNames: PropertyKey[] = Reflect.ownKeys(clazz)
  const parentClass = Object.getPrototypeOf(clazz)
  if (!!parentClass && parentClass.constructor.name !== 'Object') {
    foundedFunctionNames = [...foundedFunctionNames, ...getAllFunctionNames(parentClass)]
  }
  return foundedFunctionNames
}

const createSpyObjWithFunctionArray = <K, T extends Type<any>>(originalFunctions: JestOriginalFunction[]): JestSpyObject<K> => {
  let spyObject = {}

  for (const originalFunction of originalFunctions) {
    if (!!(originalFunction as JestExtended).originalFnName) {
      const jestFunction = originalFunction as JestExtended
      spyObject = {
        ...spyObject,
        // @ts-ignore
        [jestFunction.originalFnName]: !!jestFunction.mockFunction ? jest.fn(jestFunction.mockFunction) : jest.fn(),
      }
    } else {
      spyObject = {
        ...spyObject,
        // @ts-ignore
        [originalFunction]: jest.fn(),
      }
    }
  }
  return spyObject as any
}

export const createSpyObjFromClass = <K, T extends Type<any>>(clazz: T, ...originalFunctions: JestExtended[]): JestSpyObject<K> => {
  const functionNames = getAllFunctionNames(clazz).sort()
  if (functionNames && functionNames.length > 0) {
    let functions: JestOriginalFunction[] = functionNames.filter(functionName => functionName !== 'constructor') as string[]
    if (originalFunctions && originalFunctions.length > 0) {
      functions =
        [
          ...functions.filter(functionName => originalFunctions.find(original => original.originalFnName === functionName) === undefined),
          ...originalFunctions,
        ]
    }

    return createSpyObjWithFunctionArray<K, T>(functions)
  }
  return null
}

export const createSpyObj = <K, T extends Type<any>>(...originalFunctions: JestOriginalFunction[]): JestSpyObject<K> => {
  return createSpyObjWithFunctionArray<K, T>(originalFunctions)
}
