import { ModuleWithProviders, NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FxNotificationComponent } from './fx-notification.component'
import { BrowserModule } from '@angular/platform-browser'
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'
import { SimpleNotificationsModule } from 'angular2-notifications'
import { FxNotificationService } from './fx-notification.service'

@NgModule({
  declarations: [FxNotificationComponent],
  exports: [
    FxNotificationComponent
  ],
  imports: [
    CommonModule,
    BrowserModule,
    BrowserAnimationsModule,
    SimpleNotificationsModule.forRoot()
  ]
})
export class FxNotificationModule {
  public static forRoot(): ModuleWithProviders {
    return {
      ngModule: FxNotificationModule,
      providers: [FxNotificationService]
    }
  }
}
