import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit } from '@angular/core'
import { AuthenticationService } from '../../services/authentication.service'
import { SecurityConfig } from '../../fx-security.module'
import { Subscription } from 'rxjs'
import { SECURITY_CONFIG } from '../../domain/security-config'
import { UserService } from '../../services/user.service'
import { IconProp } from '@fortawesome/fontawesome-svg-core'
import { faUser } from '@fortawesome/free-solid-svg-icons/faUser'

@Component({
  selector: 'app-fx-user-selector',
  templateUrl: './fx-user-selector.component.html',
  styleUrls: ['./fx-user-selector.component.scss']
})
export class FxUserSelectorComponent implements OnInit, OnDestroy {

  @Input()
  public btnStyle = ''

  @Input()
  public dropDownStyling = ''

  @Input()
  public icon = ''

  public fasUser: IconProp = faUser

  public username: string

  private loginSubscription: Subscription

  private securityConfig: SecurityConfig

  constructor(private authenticationService: AuthenticationService,
    private userService: UserService,
    private changeDetector: ChangeDetectorRef,
    @Inject(SECURITY_CONFIG) securityConfig: SecurityConfig) {
    this.securityConfig = securityConfig
  }

  public ngOnInit(): void {
    this.authenticationService.loginEvents.subscribe(() => {
      this.username = this.userService.getUsername()
      this.changeDetector.markForCheck()
    })
  }

  public isNotLoggedIn(): boolean {
    return (this.username === '')
  }

  public login(): void {
    this.authenticationService.redirectToIdpLogin(this.securityConfig.login_redirect_url)
  }

  public ngOnDestroy(): void {
    if (this.loginSubscription) {
      this.loginSubscription.unsubscribe()
    }
  }

  public setIcon(selectedIcon?: string): IconProp {
    switch (selectedIcon) {
      case 'fasUser': {
        return this.fasUser
      }
      default: {
        return this.fasUser
      }
    }
  }
}
