import { BehaviorSubject } from 'rxjs'

export class ActivatedRouteMock {

  public get testParams(): {} {
    return this._testParams
  }

  public set testParams(params: {}) {
    this._testParams = params
    this.params.next(params)
  }

  public get testFragment(): string {
    return this._fragment
  }

  public set testFragment(fragment: string) {
    this._fragment = fragment
    this.fragment.next(fragment)
  }

  public get testQueryParams(): {} {
    return this._queryParams
  }

  public set testQueryParams(queryParams: {}) {
    this._queryParams = queryParams
    this.queryParams.next(queryParams)
  }

  public get snapshot(): {} {
    return {
      params: this.testParams,
      queryParams: this.testQueryParams,
      fragment: this.testFragment,
    }
  }

  public get parent(): {} {
    return {
      fragment: this.fragment,
      params: this.params,
      queryParams: this.queryParams,
    }
  }

  public fragment = new BehaviorSubject(this.testFragment)
  public params = new BehaviorSubject(this.testParams)
  public queryParams = new BehaviorSubject(this.testQueryParams)

  private _testParams: {}

  private _fragment: string

  private _queryParams: {}

}
