import { Pipe, PipeTransform } from '@angular/core'
import { TranslatePipe } from '@ngx-translate/core'

@Pipe({name: 'translateWithPluralization'})
export class TranslateWithPluralizationPipe extends TranslatePipe implements PipeTransform {

  public transform(query: any, ...args: any[]): any {
    const count = args[0]['count']
    if (count !== null && count !== undefined) {
      if (count.toString() === '1') {
        query += '.singular'
      } else {
        query += '.plural'
      }
    }
    return super.transform(query, args[0])
  }

}
