export function getBase64EncodeUrlSafe(url: string): string | undefined {
  if (!url) {
    return undefined
  }
  const base64Url = btoa(url)
  return base64Url
    .replace(/\+/g, '-')
    .replace(/\//g, '_')
    .replace(/\=/g, '_EQ_')
}

export function getBase64DecodeUrlSafe(encodedBase64Url: string): string | undefined {
  if (!encodedBase64Url) {
    return undefined
  }
  const decodedUrl = encodedBase64Url
    .replace(/_EQ_/g, '=')
    .replace(/-/g, '+')
    .replace(/_/g, '/')

  return atob(decodedUrl)
}
