import { Injectable } from '@angular/core'
import { NgbDatepickerI18n, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap'
import { LangChangeEvent, TranslateService } from '@ngx-translate/core'


const I18N_VALUES = {
  de: {
    weekdays: ['Mo', 'Di', 'Mi', 'Do', 'Fr', 'Sa', 'So'],
    months_long: ['Januar', 'Februar', 'Mrz', 'April', 'Mai', 'Juni', 'Juli', 'August', 'September', 'Oktober', 'November', 'Dezember'],
    months_short: ['Jan', 'Feb', 'Mr', 'Apr', 'Mai', 'Jun', 'Jul', 'Aug', 'Sep', 'Okt', 'Nov', 'Dez'],
  },
  en: {
    weekdays: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'],
    months_long: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
    months_short: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dez'],
  }
}

@Injectable()
export class I18n {
  public language = 'en'

  constructor(private translateService: TranslateService) {
    translateService.onLangChange.subscribe( (langChangeEvent: LangChangeEvent) => {
      this.language = langChangeEvent.lang
    })
  }
}

@Injectable()
export class FxWeekSelectorI18n extends NgbDatepickerI18n {

  constructor(private i18n: I18n) {
    super()
  }

  public getDayAriaLabel(date: NgbDateStruct): string {
    return `${date.year}-${date.month}-${date.day}`
  }

  public getMonthFullName(month: number): string {
    return I18N_VALUES[this.i18n.language].months_long[month - 1]
  }

  public getMonthShortName(month: number): string {
    return this.getMonthFullName(month)
  }

  public getWeekdayShortName(weekday: number): string {
    return I18N_VALUES[this.i18n.language].weekdays[weekday - 1]
  }

}
