import { Component, Input, OnInit, OnDestroy } from '@angular/core'
import { BoundarySelectorResource } from './domain/boundary-selector-resource'
import { IconProp } from '@fortawesome/fontawesome-svg-core'
import { faIndustry } from '@fortawesome/free-solid-svg-icons/faIndustry'
import { faBox } from '@fortawesome/free-solid-svg-icons/faBox'
import { faCar } from '@fortawesome/free-solid-svg-icons/faCar'
import { faCopyright } from '@fortawesome/free-regular-svg-icons/faCopyright'
import { BoundarySelectorDataService } from './services/boundary-selector-data.service'
import { Subscription } from 'rxjs'

@Component({
  selector: 'app-fx-boundary-selector',
  templateUrl: './fx-boundary-selector.component.html',
  styleUrls: ['./fx-boundary-selector.component.scss']
})

export class FxBoundarySelectorComponent implements OnInit, OnDestroy {

  public boundarySelectorResourceList: BoundarySelectorResource[]
  public selectedBoundary: BoundarySelectorResource

  @Input()
  public btnStyle = ''

  @Input()
  public dropDownStyling = ''

  @Input()
  public icon = ''

  public fasIndustry: IconProp = faIndustry
  public fasBox: IconProp = faBox
  public fasCar: IconProp = faCar
  public farCopyright: IconProp = faCopyright

  @Input()
  public filterable = false

  private filterInput: string

  private subscriptions: Subscription = new Subscription()

  constructor(private boundarySelectorDataService: BoundarySelectorDataService) { }

  public ngOnInit(): void {
    this.subscribeToBoundaryResourceList()
    this.subscribeToSelectedBoundary()
  }

  public ngOnDestroy(): void {
    this.subscriptions.unsubscribe()
  }

  public setSelectedBoundary(boundary: BoundarySelectorResource): void {
    this.boundarySelectorDataService.setSelectedBoundary(boundary)
  }

  public isAnyBoundaryAvailable(): boolean {
    return this.boundarySelectorResourceList != null && this.boundarySelectorResourceList.length > 0
  }

  public setIcon(selectedIcon?: string): IconProp {
    switch (selectedIcon) {
      case 'fasIndustry': {
        return this.fasIndustry
      }
      case 'fasBox': {
        return this.fasBox
      }
      case 'fasCar': {
        return this.fasCar
      }
      case 'farCopyright': {
        return this.farCopyright
      }
      default: {
        return this.fasIndustry
      }
    }
  }

  public filterList(entries: BoundarySelectorResource[]): any {
    if (this.filterable && this.filterInput && this.filterInput.length > 0) {
      return entries.filter(e => e.boundaryValueKey.concat(e.boundaryValueName).toLowerCase().includes(this.filterInput.toLowerCase()))
    } else {
      return entries
    }
  }

  public enterFilterText(searchString: string): void {
    this.filterInput = searchString
  }

  private subscribeToBoundaryResourceList(): void {
    this.subscriptions.add(this.boundarySelectorDataService.boundaryResourceList$.subscribe((boundaryList: BoundarySelectorResource[]) => {
      this.boundarySelectorResourceList = boundaryList
    }))
  }

  private subscribeToSelectedBoundary(): void {
    this.subscriptions.add(this.boundarySelectorDataService.selectedBoundary$.subscribe((boundary: BoundarySelectorResource) => {
      if (this.selectedBoundary === boundary) {
        return
      }

      this.selectedBoundary = boundary
    }))
  }

}
