import { InjectionToken } from '@angular/core'
import { FxHeaderRouteConfigModel } from './fx-header-route-config.model'
import { FxHeaderComponentConfigModel } from './fx-header-component-config.model'
import { FxHeaderAppSpecificComponentConfigModel } from './fx-header-app-specific-component-config.model'

export let FX_HEADER_CONFIG = new InjectionToken<FxHeaderConfigModel>('app.fxheaderconfig')

export interface FxHeaderConfigModel {
    logoPath?: string
    appTitleShort: string
    appTitle: string
    routes: FxHeaderRouteConfigModel[]
    appSpecificComponents?: FxHeaderAppSpecificComponentConfigModel[]
    staticComponents?: FxHeaderComponentConfigModel[]
    components?: FxHeaderComponentConfigModel[]
}
