import { Component, Input } from '@angular/core'
import { getColorByValue } from '../../../shared/helper/color-range.helper'
import { DatatableColumn } from '../../../shared/interfaces/datatable-column'
import { ColorRange } from '../../../shared/interfaces/color-range'

@Component({
  selector: 'app-fx-color-indicators-cell-template',
  templateUrl: './fx-color-indicators-cell-template.component.html',
  styleUrls: ['./fx-color-indicators-cell-template.component.scss'],
})
export class FxColorIndicatorsCellTemplateComponent {
  @Input()
  public value: number

  @Input()
  public datatableColumn: DatatableColumn

  public constructor() {
  }

  public getColorByValue(colorRanges: ColorRange[], value: number): string | undefined {
    return getColorByValue(colorRanges, value)
  }

}
