import { Component, Input } from '@angular/core'
import { NgbDropdownConfig } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Language } from '../fx-language-selector/domain/language'

export const FX_LANGUAGE = 'FX_LANGUAGE'

@Component({
  selector: 'app-fx-translation',
  templateUrl: './fx-translation.component.html',
  styleUrls: ['./fx-translation.component.scss'],
  providers: [NgbDropdownConfig]
})
export class FxTranslationComponent {

  @Input()
  public dropUpStyling = ''

  @Input()
  public btnStyle = ''

  @Input()
  public btnSize = ''

  public readonly languages: Language[] = [
    { locale: 'de', name: 'Deutsch' },
    { locale: 'en', name: 'English' }
  ]

  constructor(
    private translateService: TranslateService,
    config: NgbDropdownConfig) {
    config.placement = 'top'
    config.container = 'body'
  }

  get selectedLanguage(): Language {
    const langFromLocalStorage = localStorage.getItem(FX_LANGUAGE)
    const selectedLanguage = this.languages.find(lang => lang.locale === langFromLocalStorage)

    if (!selectedLanguage) {
      return this.languages[1]
    }

    return selectedLanguage
  }

  public select(language: Language): void {
    const selectedLanguage = this.languages.find(lang => lang.locale === language.locale)
    if (!selectedLanguage) {
      return
    }

    this.translateService.use(language.locale)
    localStorage.setItem(FX_LANGUAGE, language.locale)
  }
}
