import { ColorRange } from '../interfaces/color-range'

export function getColorByValue(colorRanges: ColorRange[], value: number): string | undefined {
  if (!colorRanges || colorRanges.length === 0) {
    return undefined
  }

  const result = colorRanges.find(colorRange => (colorRange.start == null || colorRange.start <= value) && (colorRange.end == null || colorRange.end >= value))
  return result ? result.color : undefined
}
