import { Injectable } from '@angular/core'
import { FxHeaderConfigModel } from '../models/config/fx-header-config.model'
import { FxHeaderComponentConfigModel } from '../models/config/fx-header-component-config.model'

@Injectable()
export class FxHeaderConfigValidatorService {

    constructor() { }

    public validate(config: FxHeaderConfigModel): boolean {
        if (!config.components) {
            return true
        }

        return this.checkForUniqueEventNames(config.components)
    }

    private checkForUniqueEventNames(components: FxHeaderComponentConfigModel[]): boolean {
        let isUnique = true
        const eventNames: string[] = []

        components.forEach(component => {
            if (!component.config || !component.config.outputEvents || !isUnique) {
                return
            }

            component.config.outputEvents.forEach(outputEvent => {
                if (eventNames.some(x => x === outputEvent)) {
                    console.error('the fx-header config contains duplicate event names (' + outputEvent + '). all event names need to be unique.')
                    isUnique = false
                }

                eventNames.push(outputEvent)
            })
        })

        return isUnique
    }

}
