import { Injectable, Inject } from '@angular/core'
import { SecurityConfig } from '../fx-security.module'
import { AuthenticationService } from './authentication.service'
import { SECURITY_CONFIG } from '../domain/security-config'
import { Role } from '../domain/role'

@Injectable()
export class AuthorizationService {

  public securityConfig: SecurityConfig

  constructor(private authenticationService: AuthenticationService, @Inject(SECURITY_CONFIG) securityConfig: SecurityConfig) {
    this.securityConfig = securityConfig
  }

  public hasViewRole(): boolean {
    return this.checkMultipleRoles(this.securityConfig.roles.view)
  }

  public hasViewRoleForBoundary(boundaryValue: string): boolean {
    return this.checkMultipleRolesForBoundary(this.securityConfig.roles.view, boundaryValue)
  }

  public hasEditRole(): boolean {
    return this.checkMultipleRoles(this.securityConfig.roles.edit)
  }

  public hasEditRoleForBoundary(boundaryValue: string): boolean {
    return this.checkMultipleRolesForBoundary(this.securityConfig.roles.edit, boundaryValue)
  }

  public hasDevopsRole(): boolean {
    return this.checkMultipleRoles(this.securityConfig.roles.devops)
  }

  public hasRole(roleToCheck: string): boolean {
    const payload = this.authenticationService.getPayload()
    return payload.roles.some((role: Role) =>
      role.role === roleToCheck
    )
  }

  public hasRoleForBoundary(roleToCheck: string, boundaryValue: string): boolean {
    const payload = this.authenticationService.getPayload()
    const foundRole = payload.roles.find(r => r.role === roleToCheck)

    if (!foundRole) {
      return false
    }

    const devopsRole = this.securityConfig.roles.devops.find(r => r === foundRole.role)
    if (devopsRole) {
      return true
    }

    const boundaries = foundRole.boundaries[this.securityConfig.boundary_name]
    return boundaries.some((boundary: string) => boundary === boundaryValue)
  }

  private checkMultipleRoles(roles: any[]): boolean {
    return roles.some(role => this.hasRole(role))
  }

  private checkMultipleRolesForBoundary(roles: any[], boundaryValue: string): boolean {
    return roles.some(role => this.hasRoleForBoundary(role, boundaryValue))
  }

}
