import { NgxUiLoaderService } from 'ngx-ui-loader'

export function showOrHideLoader(loading: boolean | number,
                                 ngxUiLoaderService: NgxUiLoaderService,
                                 loadingId: string,
                                 showLoader: boolean = true): void {
  if (showLoader && !!ngxUiLoaderService && !!ngxUiLoaderService.getLoaders()[loadingId]) {
    !!loading ? ngxUiLoaderService.startLoader(loadingId) : ngxUiLoaderService.stopLoader(loadingId)
  }
}
