import {
    Component,
    ComponentFactoryResolver,
    ViewChild,
    ViewContainerRef,
    AfterViewInit,
    ChangeDetectorRef,
    Output,
    EventEmitter,
    Input,
    HostListener,
    ElementRef,
    OnDestroy
} from '@angular/core'
import { FxHeaderEvent } from './models/fx-header-event'
import { FxHeaderConfigService } from './services/fx-header-config.service'
import { FxHeaderRouteConfigModel } from './models/config/fx-header-route-config.model'
import { FxHeaderComponentConfigModel } from './models/config/fx-header-component-config.model'
import { FxHeaderConfigModel } from './models/config/fx-header-config.model'
import { Subscription, of } from 'rxjs'
import { FxHeaderAppSpecificComponentConfigModel } from './models/config/fx-header-app-specific-component-config.model'
import { FxHeaderAppSpecificTextComponent } from './components/fx-header-app-specific-text.component'

@Component({
    selector: 'app-fx-header',
    templateUrl: './fx-header.component.html',
    styleUrls: ['./fx-header.component.scss']
})
export class FxHeaderComponent implements AfterViewInit, OnDestroy {

    @ViewChild('appSpecificComponentsContainer', { read: ViewContainerRef, static: false })
    public appSpecificComponentsContainer: ViewContainerRef

    @ViewChild('staticComponentsContainer', { read: ViewContainerRef, static: false })
    public staticComponentsContainer: ViewContainerRef

    @ViewChild('componentsContainer', { read: ViewContainerRef, static: false })
    public componentsContainer: ViewContainerRef


    @Input()
    public navbarStyling = 'fx-styling'
    @Output()
    public componentEvents = new EventEmitter<FxHeaderEvent>()

    public logoPath = '/assets/images/fx_logos/FX_Logo_White.png'
    public appTitleShort: string
    public appTitle: string
    public routes: FxHeaderRouteConfigModel[] = []
    public appSpecificComponentsConfigs: FxHeaderAppSpecificComponentConfigModel[] = []
    public staticComponentConfigs: FxHeaderComponentConfigModel[] = []
    public componentConfigs: FxHeaderComponentConfigModel[] = []

    public isCollapsed = true

    private subscriptions: Subscription = new Subscription()

    constructor(private fxHeaderConfigService: FxHeaderConfigService,
        private componentFactoryResolver: ComponentFactoryResolver,
        private cdRef: ChangeDetectorRef,
        private eRef: ElementRef) {
    }

    public ngAfterViewInit(): void {
        this.subscriptions.add(this.fxHeaderConfigService.config.subscribe((config: FxHeaderConfigModel) => {
            if (config.logoPath != null) {
                this.logoPath = config.logoPath
            }

            this.appTitleShort = config.appTitleShort
            this.appTitle = config.appTitle
            this.routes = config.routes

            if (config.appSpecificComponents) {
                config.appSpecificComponents.forEach((componentConfig: FxHeaderAppSpecificComponentConfigModel) => {
                    this.appSpecificComponentsConfigs.push(componentConfig)
                    this.addAppSpecificComponentToContainer(componentConfig)
                })
            }

            if (config.staticComponents) {
                config.staticComponents.forEach((component: FxHeaderComponentConfigModel) => {
                    this.staticComponentConfigs.push(component)
                    this.addComponentToContainer(component, this.staticComponentsContainer)
                })
            }

            if (config.components) {
                config.components.forEach((componentConfig: FxHeaderComponentConfigModel) => {
                    this.componentConfigs.push(componentConfig)
                    this.addComponentToContainer(componentConfig, this.componentsContainer)
                })
            }
        }))

        this.cdRef.detectChanges()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    @HostListener('document:click', ['$event'])
    public clickout(event: Event): void {
        if (!this.eRef.nativeElement.contains(event.target)) {
            this.isCollapsed = true
        }
    }

    private addAppSpecificComponentToContainer(componentConfig: FxHeaderAppSpecificComponentConfigModel): void {
        this.addComponentToContainer(componentConfig.componentConfigModel, this.appSpecificComponentsContainer)
        this.addComponentToContainer(this.creatTextComponentConfig(componentConfig.text), this.appSpecificComponentsContainer)
    }

    private creatTextComponentConfig(text: string): FxHeaderComponentConfigModel {
        return {
            componentClass: FxHeaderAppSpecificTextComponent,
            config: { inputs: of([{ key: 'text', value: text }]) }
        }
    }

    private addComponentToContainer(componentConfig: FxHeaderComponentConfigModel, componentContainer: ViewContainerRef): void {
        const componentFactory = this.componentFactoryResolver.resolveComponentFactory(componentConfig.componentClass)
        const component = componentContainer.createComponent(componentFactory)

        if (!componentConfig.config) {
            return
        }

        if (componentConfig.config.inputs) {
            this.subscriptions.add(componentConfig.config.inputs.subscribe(config => {
                config.forEach(configEntry => {
                    component.instance[configEntry.key] = configEntry.value
                })
            }))
        }

        if (componentConfig.config.outputEvents) {
            componentConfig.config.outputEvents.forEach((eventName) => {
                this.subscriptions.add(component.instance[eventName].subscribe(event => {
                    this.componentEvents.emit({ name: eventName, data: event })
                }))
            })
        }
    }

}
