import { Injectable } from '@angular/core'
import { Notification, NotificationsService, NotificationType } from 'angular2-notifications'
import { forkJoin, Observable, Observer } from 'rxjs'
import { TranslateService } from '@ngx-translate/core'
import { NavigationEnd, Router } from '@angular/router'
import { filter } from 'rxjs/operators'

const i18nPrefix = 'i18n.fx-components.notification.'

const configurations = {
  error: {
    timeOut: 5000
  },
  warn: {
    timeOut: 5000
  },
  info: {
    timeOut: 3000
  },
  success: {
    timeOut: 3000
  }
}

@Injectable()
export class FxNotificationService {

  public errorNotifications: Notification[] = []

  constructor(private notifications: NotificationsService,
    private translate: TranslateService,
    router: Router) {
    router.events
      .pipe(
        filter(event => event instanceof NavigationEnd)
      )
      .subscribe(() => {
        this.errorNotifications.forEach((notification: Notification) =>
          notifications.remove(notification.id)
        )
        this.errorNotifications = []
      })

  }

  public error(messageType: string): void {
    this.create(NotificationType.Error, messageType)
      .subscribe((notification: Notification) =>
        this.errorNotifications.push(notification)
      )
  }

  public warn(messageType: string): void {
    this.create(NotificationType.Warn, messageType).subscribe()
  }

  public info(messageType: string): void {
    this.create(NotificationType.Info, messageType).subscribe()
  }

  public success(messageType: string): void {
    this.create(NotificationType.Success, messageType).subscribe()
  }

  public httpError(statusCode: number, statusCodeDescriptionsType?: string): void {
    if (!statusCodeDescriptionsType) {
      statusCodeDescriptionsType = 'default'
    }
    const titleObservable = this.translateMessage(NotificationType.Error, 'type')
    const errorMessageObservable = this.translateMessage(NotificationType.Error, 'http.' + statusCodeDescriptionsType + '.' + statusCode)
    this.generateNotification(NotificationType.Error, titleObservable, errorMessageObservable).subscribe()
  }

  private create(type: NotificationType, messageType: string): Observable<Notification> {
    const titleObservable: Observable<string> = this.translateMessage(type, messageType + '.title')
    const messageObservable: Observable<string> = this.translateMessage(type, messageType + '.message')
    return this.generateNotification(type, titleObservable, messageObservable)
  }

  private generateNotification(
    type: NotificationType, titleObservable: Observable<string>, messageObservable: Observable<string>): Observable<Notification> {
    return new Observable((observer: Observer<Notification>) => {

      forkJoin([titleObservable, messageObservable]).subscribe((result: string[]) => {
        const title: string = result[0]
        const message: string = result[1]

        const notification = this.notifications.create(title, message, type, configurations[type])
        observer.next(notification)
        observer.complete()
      })

    })
  }

  private translateMessage(type: NotificationType, i18nMessage: string): Observable<string> {
    return this.translate.get(i18nPrefix + type + '.' + i18nMessage)
  }
}
