import { Injectable, Injector } from '@angular/core'
import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http'
import { Observable } from 'rxjs'
import { AuthenticationService } from './authentication.service'

@Injectable()
export class AuthenticatingInterceptorService implements HttpInterceptor {

  constructor(private injector: Injector) { }

  public intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const authenticationService: AuthenticationService = this.injector.get(AuthenticationService)
    let newRequest = request
    if (authenticationService.isLoggedIn) {
      newRequest = request.clone({
        setHeaders: {
          Authorization: authenticationService.getAuthTokenHeaderString()
        }
      })
    } else {
      newRequest = request
    }
    return next.handle(newRequest)
  }

}
