import { get } from 'lodash'
import { Observable, of, Subject } from 'rxjs'

export interface ITranslateServiceMock {
  onTranslationChange: Observable<any>
  onLangChange: Observable<any>
  onDefaultLangChange: Observable<any>

  get(key: string | Array<string>, param?: Object): Observable<any>

  instant(key: string): any

  getBrowserLang(): string

  getBrowserCultureLang(): string

  getTranslation(lang: string): Observable<any>
}

export interface ITranslateServiceMockConfig {
  browserCulterLang?: string
  currentLang?: string
  translation?: { [key: string]: any }
  translations?: { [key: string]: any }
}

export class TranslateServiceMockImpl implements ITranslateServiceMock {
  public onDefaultLangChange: Observable<any>
  public onLangChange: Subject<any>
  public onTranslationChange: Observable<any>

  public get currentLang(): string {
    return get(this._config, 'currentLang', this.getBrowserCultureLang().substr(0, 2))
  }

  public constructor(private _config: ITranslateServiceMockConfig) {
    this.onLangChange = new Subject<any>()
  }

  public get(key: string | Array<string>, param?: Object): Observable<any> {
    if (Array.isArray(key)) {
      const keyMap = {}
      key.forEach(k => {
        keyMap[k] = this.instant(k)
      })
      return of(keyMap)
    }
    return of(this.instant(key))
  }

  public switchLanguage(language: any): void {
    this.onLangChange.next(language)
  }

  public getBrowserCultureLang(): string {
    return get(this._config, 'browserCultureLang', 'de-DE')
  }

  public getBrowserLang(): string {
    const [browserLang]: string[] = this.getBrowserCultureLang().split('-')
    return browserLang
  }

  public getTranslation(lang: string): Observable<any> {
    return of(get(this._config, `translation.${lang}`, lang))
  }

  public instant(key: string): any {
    return get(this._config, `translations.${key}`, key)
  }

}

export const translateServiceMock = new TranslateServiceMockImpl({})

export function createTranslateServiceMock(config: ITranslateServiceMockConfig): ITranslateServiceMock {
  return new TranslateServiceMockImpl(config)
}
