import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FxBoundarySelectorComponent } from './fx-boundary-selector.component'
import { RouterModule } from '@angular/router'
import { FormsModule } from '@angular/forms'
import { NgbModule } from '@ng-bootstrap/ng-bootstrap'
import { HttpClientModule } from '@angular/common/http'
import { BrowserModule } from '@angular/platform-browser'
import { FxTranslationModule } from '../fx-translation/fx-translation.module'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faIndustry as fasIndustry } from '@fortawesome/free-solid-svg-icons/faIndustry'
import { faBox as fasBox } from '@fortawesome/free-solid-svg-icons/faBox'
import { faCar as fasCar } from '@fortawesome/free-solid-svg-icons/faCar'
import { faCopyright as farCopyright } from '@fortawesome/free-regular-svg-icons/faCopyright'
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome'
import { BoundarySelectorResolver } from './services/boundary-selector-resolver.service'
import { BoundarySelectorDataService } from './services/boundary-selector-data.service'
import { BoundaryDataService } from './services/boundary-data.service'
import { FxSearchInputModule } from '../fx-search-input/fx-search-input.module'

library.add(fasIndustry, fasBox, fasCar, farCopyright)

@NgModule({
  declarations: [FxBoundarySelectorComponent],
  imports: [
    BrowserModule,
    HttpClientModule,
    CommonModule,
    RouterModule,
    FormsModule,
    NgbModule,
    FxTranslationModule,
    FontAwesomeModule,
    FxSearchInputModule
  ],
  providers: [
    BoundarySelectorDataService,
    BoundarySelectorResolver,
    BoundaryDataService
  ],
  exports: [FxBoundarySelectorComponent]
})
export class FxBoundarySelectorModule {
}
