import { Component, OnDestroy, OnInit } from '@angular/core'
import { Router } from '@angular/router'
import { NotificationsService } from 'angular2-notifications'
import { NEVER, Subscription } from 'rxjs'
import { catchError } from 'rxjs/operators'
import { AuthenticationService, IdpIdentityToken } from '../../services/authentication.service'

@Component({
  template: '',
})
export class IdpLoginComponent implements OnInit, OnDestroy {

  private subscription = new Subscription()

  constructor(private authenticationService: AuthenticationService,
              private router: Router,
              private notificationService: NotificationsService,
  ) {
  }

  public ngOnInit(): void {

    this.subscription.add(this.authenticationService.loginFromUrlToken(window.location.href)
      .pipe(
        catchError(error => {
          this.notificationService.error(error)
          return NEVER
        }),
      )
      .subscribe((identityToken: IdpIdentityToken) => {
        this.router.navigateByUrl(identityToken.state)
      }),
    )
  }

  public ngOnDestroy(): void {
    this.subscription.unsubscribe()
  }

}
