import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FxWikiComponent } from './fx-wiki.component'
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faQuestionCircle as fasQuestionCircle } from '@fortawesome/free-solid-svg-icons/faQuestionCircle'
import { faQuestionCircle as farQuestionCircle } from '@fortawesome/free-regular-svg-icons/faQuestionCircle'
import { faQuestion as fasQuestion } from '@fortawesome/free-solid-svg-icons/faQuestion'

library.add(fasQuestionCircle, farQuestionCircle, fasQuestion)

@NgModule({
  declarations: [FxWikiComponent],
  exports: [
    FxWikiComponent
  ],
  imports: [
    CommonModule,
    FontAwesomeModule
  ]
})
export class FxWikiModule {
}

