import { formatDate } from '@angular/common'
import { Component, ElementRef, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, TemplateRef, ViewChild } from '@angular/core'
import { TranslateService } from '@ngx-translate/core'
import { DataTableColumnDirective, DatatableComponent, SortType } from '@swimlane/ngx-datatable'
import { ClipboardService } from 'ngx-clipboard'
import { ContextMenuComponent } from 'ngx-contextmenu'
import { NgxUiLoaderService } from 'ngx-ui-loader'
import { PositionType, SpinnerType } from 'ngx-ui-loader/lib/utils/types'
import { Subject, Subscription } from 'rxjs'
import { debounceTime, filter, map } from 'rxjs/operators'
import { FxNotificationService } from '../fx-notification/fx-notification.service'
import { showOrHideLoader } from '../shared/helper/loading.helper'
import { getTooltip } from '../shared/helper/tooltip.helper'

import { DatatableColumn, DatatableColumnBorder, DatatableColumnFoldColumns, DatatableColumnTooltip } from '../shared/interfaces/datatable-column'
import { DatatableRowDetail } from '../shared/interfaces/datatable-row-detail'
import { PageInfo } from '../shared/interfaces/page-info'
import { ScrollInfo } from '../shared/interfaces/scroll-info'
import { SortInfo } from '../shared/interfaces/sort-info'

type RowHeightFunctionType = (row?: any, index?: number) => number

@Component({
  selector: 'app-fx-custom-table',
  templateUrl: './fx-custom-table.component.html',
  styleUrls: ['./fx-custom-table.component.scss'],
})

export class FxCustomTableComponent implements OnInit, OnChanges, OnDestroy {
  @ViewChild('defaultCellTemplate', {static: true})
  public defaultCellTemplate: TemplateRef<any>

  @ViewChild('defaultHeaderTemplate', {static: true})
  public defaultHeaderTemplate: TemplateRef<any>

  @ViewChild('datatable', {static: true})
  public datatable: DatatableComponent

  @ViewChild('debugContextMenu', {static: true})
  public debugContextMenu: ContextMenuComponent

  @ViewChild('selectableContextMenu', {static: true})
  public selectableContextMenu: ContextMenuComponent

  @Input()
  public loading: number | boolean = 0

  @Input()
  public columnMode = 'force'

  @Input()
  public showLoader = false

  @Input()
  public loadingId = 'custom-table'

  @Input()
  public hasProgressBar = false

  @Input()
  public foregroundColor = '#004666'

  @Input()
  public overlayColor = 'rgba(255,255,255,0.9)'

  @Input()
  public loadingType: SpinnerType = 'circle'

  @Input()
  public loadingPosition: PositionType = 'center-center'

  @Input()
  public loaderSize = 70

  @Input()
  public columns: DatatableColumn[] = []

  @Input()
  public messagesEmptyMessage = 'i18n.common.table.messages.emptyMessage'

  @Input()
  public messagesTotalMessage = 'i18n.common.table.messages.totalMessage'

  @Input()
  public messagesSelectedMessage = 'i18n.common.table.messages.selectedMessage'

  @Input()
  public count = 0

  @Input()
  public offset = 0

  @Input()
  public limit: number | undefined = 20

  @Input()
  public headerHeight: number | 'auto' = 50

  @Input()
  public rows: any[]

  @Input()
  public externalPagingLoadedPage: number

  @Input()
  public footerHeight: number

  @Input()
  public rowHeight = 25

  @Input()
  public rowClassCallback: (row: any) => { [key: string]: boolean }

  @Input()
  public rowHeightCallback = ((row, index): number => {
    if (typeof this.rowDetail.rowHeight === 'function') {
      return (this.rowDetail.rowHeight as RowHeightFunctionType)(this.rows[index], index)
    }
    return (this.rowDetail.rowHeight as number)
  })

  @Input()
  public externalPaging = true

  @Input()
  public externalSorting = true

  @Input()
  public sorts: any[]

  @Input()
  public sortType = SortType.single

  @Input()
  public scrollbarH = true

  @Input()
  public scrollbarV = false

  @Input()
  public preloadNextPage = true

  @Input()
  public preloadPrevPage = true

  @Input()
  public rowDetail: DatatableRowDetail

  @Input()
  public isDebugContextMenuVisible: boolean

  @Input()
  public loadingPageDebounceTimeInMilliseconds = this.externalPaging ? 200 : 0

  @Output()
  public pageCallback = new EventEmitter<PageInfo>()

  @Output()
  public sortCallback = new EventEmitter<SortInfo>()

  @Output()
  public scrollCallback = new EventEmitter<ScrollInfo>()

  @Output()
  public selectRowCallback = new EventEmitter<{ row: any, column: any }>()

  public tableRows: any[]

  public originalColumns: DatatableColumn[]

  public loadedEntries: number

  public pageInfo: PageInfo = {
    offset: 0,
    pageSize: 0,
  }

  public fetchDataDebouncer = new Subject<PageInfo>()

  private loadingPages = new Map<number, boolean>()

  private subscriptions: Subscription = new Subscription()

  public constructor(
    private clipboardService: ClipboardService,
    private elRef: ElementRef,
    private ngxUiLoaderService: NgxUiLoaderService,
    private notificationService: FxNotificationService,
    private translate: TranslateService,
  ) {
  }

  public ngOnDestroy(): void {
    this.subscriptions.unsubscribe()
  }

  public ngOnChanges(changes: SimpleChanges): void {
    if (changes.loading) {
      this.handleLoading()
    }

    if (changes.count) {
      if (this.externalPaging) {
        if (this.count > 0) {

          this.tableRows = new Array<any>(this.count)
          this.loadingPages = new Map<number, boolean>()

          this.goToFirstPage()
        }
      }
    }

    if (changes.rows) {
      if (!this.externalPaging) {
        this.tableRows = this.rows
        return
      }

      if (this.tableRows) {
        const start = (this.offset || this.pageInfo.offset) * this.pageInfo.pageSize
        this.tableRows.splice(start, this.rows.length, ...this.rows)
        if (this.isDebugContextMenuVisible) {
          this.loadedEntries = this.tableRows.filter(row => !!row).length
        }
      }
    }
  }

  public ngOnInit(): void {
    this.handleInitialColumnFolding()

    this.subscriptions.add(this.fetchDataDebouncer
      .pipe(
        filter(pageInfo => !!pageInfo),
        map(pageInfo => this.getNextPage(pageInfo, 0)),
        map(pageInfo => {
          this.pageInfo = pageInfo

          const maxOffset = Math.max(0, Math.ceil(pageInfo.count / pageInfo.pageSize) - 1)
          const isFirstPage = pageInfo.offset === 0
          const isLastPage = pageInfo.offset === maxOffset

          const currentAllDataIsFilled = this.isAllDataFilled(this.tableRows, pageInfo)

          let prevAllDataIsFilled = true
          let newPageInfoPrev: PageInfo
          if (isLastPage || this.preloadPrevPage) { // pre-load previous page
            newPageInfoPrev = this.getNextPage(pageInfo, -1)
            prevAllDataIsFilled = this.isAllDataFilled(this.tableRows, newPageInfoPrev)
          }

          let nextAllDataIsFilled = true
          let newPageInfoNext: PageInfo
          if (isFirstPage || this.preloadNextPage) { // pre-load next page
            newPageInfoNext = this.getNextPage(pageInfo, 1)
            nextAllDataIsFilled = this.isAllDataFilled(this.tableRows, newPageInfoNext)
          }
          return {
            currentAllDataIsFilled,
            pageInfoCurrent: pageInfo,
            prevAllDataIsFilled,
            pageInfoPrev: newPageInfoPrev,
            nextAllDataIsFilled,
            pageInfoNext: newPageInfoNext,
          }
        }),
        filter(pageInfo => !pageInfo.currentAllDataIsFilled || !pageInfo.prevAllDataIsFilled || !pageInfo.nextAllDataIsFilled),
        debounceTime(this.loadingPageDebounceTimeInMilliseconds || 0),
      )
      .subscribe(pageInfo => {
        this.getNewData(pageInfo.pageInfoPrev, pageInfo.prevAllDataIsFilled)
        this.getNewData(pageInfo.pageInfoCurrent, pageInfo.currentAllDataIsFilled)
        this.getNewData(pageInfo.pageInfoNext, pageInfo.nextAllDataIsFilled)

        this.handleLoading()
      }),
    )
  }

  public getFilteredColumns(originalColumns: DatatableColumn[], expectedFoldState: boolean = false): DatatableColumn[] {
    if (!originalColumns) {
      return undefined
    }
    return originalColumns.filter(c => expectedFoldState ? c.isFolded : !c.isFolded) // isFolded could also be null or undefined (it equal 'false')
  }

  public selectRow(row: any, column: DatatableColumn): void {
    this.selectRowCallback.emit({row: row, column: column})
  }

  public getStaticTooltip(tooltip: DatatableColumnTooltip | DatatableColumnTooltip[], row?: any): string | undefined {
    return getTooltip(tooltip, this.translate, row)
  }

  public getRowTooltip(noTextOverflow: boolean, dataColumn: DatatableColumn, row: any, value: any): string {
    if ('tooltip' in row && dataColumn.name in row.tooltip) {
      return row.tooltip[dataColumn.name]
    }
    if (noTextOverflow) {
      return value
    }
    return ''
  }

  public isTooltipDisabled(divElement: HTMLDivElement, row: any, dataColumn: DatatableColumn): boolean {
    return (divElement && divElement.offsetWidth >= divElement.scrollWidth
      && !('tooltip' in row && dataColumn.name in row.tooltip))
  }

  public getBorderClass(border: DatatableColumnBorder): string[] {
    const borderClasses: string[] = []
    if (border && (border.bottom || border.left || border.right || border.top)) {
      if (border.bottom) {
        borderClasses.push('border-bottom')
      }
      if (border.left) {
        borderClasses.push('border-left')
      }
      if (border.right) {
        borderClasses.push('border-right')
      }
      if (border.top) {
        borderClasses.push('border-top')
      }
    }
    return borderClasses
  }

  public getCellStyleClasses(baseClass: string | ((data: any) => string | any), data: any): string[] {
    const additionalClass: string = (typeof baseClass === 'function') ? baseClass(data) : baseClass
    return additionalClass ? [additionalClass] : []
  }

  public getCellClasses(dataColumn: DatatableColumn): string {
    const cellClasses: string[] = []

    if (dataColumn.cellAlign) {
      cellClasses.push('align__' + dataColumn.cellAlign)
    }
    const borderClasses = this.getBorderClass(dataColumn.cellBorder)
    if (borderClasses.length > 0) {
      cellClasses.push(...borderClasses)
    }

    if (dataColumn.isFolded === false) {
      cellClasses.push('unfolded')
    }

    if (dataColumn.cellClass && typeof dataColumn.cellClass !== 'function') {
      cellClasses.push(dataColumn.cellClass as string)
    }
    return cellClasses.join(' ')
  }

  public getHeaderClasses(dataColumn: DatatableColumn): string {
    const headerClasses: string[] = []

    if (dataColumn.headerAlign) {
      headerClasses.push('align__' + dataColumn.headerAlign)
    }
    const borderClasses = this.getBorderClass(dataColumn.headerBorder)
    if (borderClasses.length > 0) {
      headerClasses.push(...borderClasses)
    }

    if (dataColumn.isFolded === false) {
      headerClasses.push('unfolded')
    }

    if (dataColumn.headerClass && typeof dataColumn.headerClass !== 'function') {
      headerClasses.push(dataColumn.headerClass as string)
    }

    if (dataColumn.foldColumns && dataColumn.foldColumns.isFolded === false) {
      headerClasses.push('fold-trigger-active')
    }
    return headerClasses.join(' ')
  }

  public trimString(value: any): string | any {
    if (!!value && typeof value === 'string' || value instanceof String) {
      return value.trim()
    }
    return value
  }

  public goToFirstPage(): void {
    // sorry for this hack but no other "solution" does work 100% :(
    const firstTabButton = this.elRef.nativeElement.querySelector('.datatable-icon-prev')
    if (!!firstTabButton) {
      firstTabButton.click()
    }
  }

  public emitScrollCallback(scrollInfo: ScrollInfo): void {
    scrollInfo = {
      ...scrollInfo,
      page: this.pageInfo.offset,
      size: this.pageInfo.pageSize,
    }

    this.scrollCallback.emit(scrollInfo)
  }

  public emitPageCallback(pageInfo: PageInfo, indexes: { first: number, last: number }): void {
    if (!pageInfo) {
      return
    }
    if (this.pageInfo.pageSize !== pageInfo.pageSize) {
      this.loadingPages = new Map<number, boolean>()
    }

    this.pageInfo = {
      ...pageInfo,
      ...indexes,
    }

    if (this.externalPaging) {
      this.fetchDataDebouncer.next(this.pageInfo)
    } else {
      this.pageCallback.emit(this.pageInfo)
    }
  }

  public copyLoadedValuesToClipboard(columnData: DataTableColumnDirective): void {
    const prop = columnData.prop
    if (!!prop) {
      const values = this.tableRows.reduce((result, column) => (result += (result === '' ? '' : ', ') + column[prop], result), '')

      this.copyValueToClipboard(values)
    }
  }

  public copyValueToClipboard(value: string): void {
    if (!!value) {
      this.clipboardService.copyFromContent(value)
      this.notificationService.success('copiedToClipboard')
    }
  }

  public evaluateFormat(format: any, value: any): any {
    if (typeof format === 'function') {
      return format(value)
    }
    return format && value ? formatDate(value, format, this.translate.currentLang) : value
  }

  public handleColumnFolding(datatableColumn: DatatableColumn): void {
    if (!this.originalColumns && this.columns) {
      this.originalColumns = this.columns
    } else if ((!this.originalColumns && !this.columns) || !datatableColumn.foldColumns) {
      return
    }
    this.setFoldStateForColumns(datatableColumn.foldColumns, !datatableColumn.foldColumns.isFolded)
    this.columns = this.getFilteredColumns(this.originalColumns)
  }

  public handleInitialColumnFolding(): void {
    if (!this.originalColumns && this.columns) {
      this.originalColumns = this.columns
    } else if (!this.originalColumns && !this.columns) {
      return
    }
    this.originalColumns.forEach(datatableColumn => {
      this.setFoldStateForColumns(datatableColumn.foldColumns)
    })
    this.columns = this.getFilteredColumns(this.originalColumns)
  }

  private setFoldStateForColumns(foldColumns: DatatableColumnFoldColumns, newFoldState?: boolean): void {
    if (!foldColumns || !foldColumns.columnNames) {
      return
    }
    foldColumns.isFolded = newFoldState != null ? newFoldState : foldColumns.isFolded
    foldColumns.columnNames.forEach(columnName => {
      const column = this.originalColumns.find(col => col.name === columnName)
      column.isFolded = foldColumns.isFolded
    })
  }

  private getNewData(pageInfo: PageInfo, isAllDataFilled: boolean): void {
    if (!pageInfo) {
      return
    }

    if (isAllDataFilled) {
      this.loadingPages.delete(pageInfo.offset)
      return
    }

    if (!this.loadingPages.get(pageInfo.offset)) {
      this.loadingPages.set(pageInfo.offset, true)
      this.pageCallback.emit(pageInfo)
    }
  }

  private getNextPage(pageInfo: PageInfo, direction: number): PageInfo {
    const maxOffset = Math.max(0, Math.ceil(pageInfo.count / pageInfo.pageSize) - 1)
    const offset = Math.min(Math.max(0, pageInfo.offset + direction), maxOffset)

    const first = Math.max(0, pageInfo.first + (pageInfo.pageSize * direction))
    const last = Math.max(first, Math.min(pageInfo.count - 1, pageInfo.last + (pageInfo.pageSize * direction)))

    const newPageInfo: PageInfo = {
      ...pageInfo,
      first,
      last,
      offset,
    }
    return newPageInfo
  }

  private isAllDataFilled(dataRows: any[], pageInfo: PageInfo): boolean {
    if (!dataRows || !pageInfo) {
      return false
    }
    if (pageInfo.first >= pageInfo.last) {
      return false
    }

    for (let i = pageInfo.first; i <= pageInfo.last; i++) {
      if (!dataRows[i]) {
        return false
      }
    }
    return true
  }

  private handleLoading(): void {
    showOrHideLoader(this.loading, this.ngxUiLoaderService, this.loadingId, this.showLoader)
    this.hasProgressBar = this.loading > 1
  }
}
